html {
  box-sizing: border-box;
  font-size: 100%;
  height: 100vh;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  background-color: #cecece;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
  height: 100vh;
  width: 100%;
  display: flex;
}

.webapp.version--two .aside {
  max-width: 25%;
}
.webapp.version--two .aside .sidenav {
  max-width: 25%;
}
.webapp.version--two .aside .subnav .form-control {
  padding: 0 25px;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
  padding: 0;
  margin: 0;
}

.icon {
  display: flex;
  flex: 0 0 auto;
}
.icon--browse {
  width: 41px;
  height: 41px;
  background-image: url("../dist/img/icon_browse.png");
}
.icon--attraction {
  width: 44px;
  height: 44px;
  background-image: url("../dist/img/icon_attraction.png");
}
.icon--video {
  width: 43px;
  height: 40px;
  background-image: url("../dist/img/icon_video.png");
}
.icon--blog {
  width: 41px;
  height: 41px;
  background-image: url("../dist/img/icon_blog.png");
}
.icon--about {
  width: 36px;
  height: 36px;
  background-image: url("../dist/img/icon_about.png");
}
.icon--setting {
  background-image: url("../dist/img/icon_setting.png");
}
.icon--question {
  background-image: url("../dist/img/icon_question.png");
}
.icon--close {
  background-image: url("../dist/img/icon_close.png");
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: "";
}

.wrapper {
  position: relative;
}

.form .form-group {
  margin: 0;
}
.form .form-group .wrapper .icon--caret-down {
  color: #a9abaf;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}
.form .form-group .form-control {
  height: 40px;
  border: 1px solid #1c1f27;
  border-radius: 0;
  background-color: transparent;
  font-size: 16px;
  line-height: 21px;
  font-weight: normal;
  font-family: "Roboto", sans-serif;
  color: #647d80;
  padding: 0 36px;
}
.form .form-group .form-control:focus {
  box-shadow: none;
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.25rem;
}

.aside {
  max-width: 500px;
  width: 100%;
  height: 100%;
  display: flex;
}
.aside .sidenav {
  max-width: 150px;
  width: 100%;
  height: 100%;
  flex: 0 0 auto;
  background-color: #313541;
}
.aside .sidenav .list__item {
  height: 125px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0 20px;
  background-color: inherit;
}
.aside .sidenav .list__item.active {
  background-color: #72cdd2;
}
.aside .sidenav .list__item.active .title {
  color: #ffffff;
}
.aside .sidenav .list__item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  border-bottom: 1px solid #282c37;
}
.aside .sidenav .list__item:last-child::after {
  border-bottom: none;
}
.aside .sidenav .list__item .icon {
  background-size: cover;
  background-repeat: no-repeat;
  margin: 0 0 10px;
}
.aside .sidenav .list__item .title {
  font-size: 16px;
  line-height: 21px;
  font-weight: normal;
  font-family: "Roboto", sans-serif;
  margin: 0;
  color: #647d80;
  text-align: center;
}
.aside .sidenav .list__item .link {
  width: 100%;
  display: flex;
  flex: 0 0 auto;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: inherit;
  text-decoration: none;
}
.aside .subnav {
  flex: 1 1 auto;
  background-color: #282c37;
  height: 100%;
  overflow: auto;
}
.aside .subnav__header {
  padding: 42.5px 25px;
  position: relative;
}
.aside .subnav__header::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  border-bottom: 1px solid #1c1f27;
}
.aside .subnav__body {
  padding: 0px 25px;
}
.aside .subnav__body .list__item {
  position: relative;
  padding: 10px 0;
}
.aside .subnav__body .list__item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  border-bottom: 1px solid #1c1f27;
}
.aside .subnav__body .list__item:last-child::after {
  border-bottom: none;
}
.aside .subnav__body .list__item .link {
  text-decoration: none;
  padding: 15px 15px 15px 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #ffffff;
  background-color: transparent;
}
.aside .subnav__body .list__item .link.active {
  background-color: #1c1f27;
}
.aside .subnav__body .list__item .link.active .title {
  color: #92d72e;
}
.aside .subnav__body .list__item .link .title {
  font-size: 16px;
  line-height: 21px;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  margin: 0;
  color: #ffffff;
}
.aside .subnav__body .list__item .link .icon--caret-down {
  color: #a9abaf;
  transform: rotate(180deg);
  transition: transform 400ms ease-in-out;
}
.aside .subnav__body .list__item .link.collapsed .icon--caret-down {
  transform: rotate(0deg);
  transition: transform 400ms ease-in-out;
}
.aside .subnav__body .list__item .sub-list .list__item {
  padding: 12.5px 20px;
}
.aside .subnav__body .list__item .sub-list .list__item :first-child {
  padding-top: 0;
}
.aside .subnav__body .list__item .sub-list .list__item :last-child {
  padding-bottom: 0;
}
.aside .subnav__body .list__item .sub-list .list__item::after {
  border-bottom: none;
}
.aside .subnav__body .list__item .sub-list .list__item .link {
  font-size: 16px;
  line-height: 21px;
  font-weight: normal;
  font-family: "Roboto", sans-serif;
  margin: 0;
  color: #647d80;
}

.main {
  flex: 1 1 auto;
}
.main .header {
  background-color: #f4f7fa;
  padding: 0px 50px;
  height: 125px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.main .header .title {
  color: #282c37;
  font-size: 24px;
  line-height: 21px;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
  margin: 0;
}
.main .header .action-wrapper {
  display: flex;
}
.main .header .action-wrapper .icon {
  margin: 0 10px;
  cursor: pointer;
  width: 31px;
  height: 31px;
  background-size: cover;
  background-repeat: no-repeat;
}
.main .header .action-wrapper .icon:first-child {
  margin-left: 0;
}
.main .header .action-wrapper .icon:last-child {
  margin-right: 0;
}
.main .section-map {
  position: relative;
  height: calc(100% - 125px);
}
.main .section-map .map {
  height: 100%;
}
.main .section-map .map .gm-style-iw.gm-style-iw-c {
  padding: 0 !important;
  background-color: transparent;
  box-shadow: none;
  border-radius: 0;
}
.main .section-map .map .gm-style-iw.gm-style-iw-c .gm-style-iw-d {
  padding: 0 !important;
  overflow: hidden !important;
}
.main .section-map .map .gm-style-iw.gm-style-iw-c button.gm-ui-hover-effect {
  display: none !important;
}
.main .section-map .map .gm-style .gm-style-iw-t::after {
  background: none;
  box-shadow: none;
}
.main .section-map .map .map-info {
  padding: 10px;
  background-color: #1c1f27;
  max-width: 220px;
  border-radius: 20px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.main .section-map .map .map-info .title {
  font-size: 13px;
  line-height: normal;
  font-weight: 15px;
  font-family: "Roboto", sans-serif;
  margin: 0;
  color: #ffffff;
}
.main .section-map .map .map-info .subtitle {
  display: none;
  font-size: 13px;
  line-height: normal;
  font-weight: 15px;
  font-family: "Roboto", sans-serif;
  margin: 0;
  color: #ffffff;
}
.main .section-map .map .map-info.active {
  background-color: #92d72e;
  padding: 15px 30px 15px 55px;
  border-radius: 60px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.main .section-map .map .map-info.active .title {
  font-size: 20px;
  margin: 0 0 10px;
}
.main .section-map .map .map-info.active .subtitle {
  display: block;
}
.main .section-map .overlay {
  display: none;
  flex-direction: column;
  align-items: stretch;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  max-width: 350px;
  width: 100%;
  overflow: auto;
}
.main .section-map .overlay__image-wrapper {
  width: 100%;
  height: 240px;
  overflow: hidden;
  margin: 0;
  flex: 0 0 auto;
}
.main .section-map .overlay__image {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}
.main .section-map .overlay__title-wrapper {
  background-color: #72cdd2;
  padding: 15px 35px;
}
.main .section-map .overlay__title {
  font-size: 24px;
  line-height: normal;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  margin: 0;
  color: #ffffff;
}
.main .section-map .overlay__info {
  padding: 25px 35px;
  background-color: #313541;
  flex: 1 1 auto;
}
.main .section-map .overlay__info .text {
  font-size: 16px;
  line-height: normal;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  margin: 0 0 22px;
  color: #cccccc;
}
.main .section-map .overlay__wrapper {
  display: flex;
  align-items: center;
  margin: 0 0 22px;
}
.main .section-map .overlay__wrapper:last-child {
  margin: 0;
}
.main .section-map .overlay__wrapper .icon {
  margin-right: 12px;
}
.main .section-map .overlay__wrapper .icon--location {
  color: #72cdd2;
}
.main .section-map .overlay__wrapper .icon--globe {
  color: #92d72e;
}
.main .section-map .overlay__wrapper .text {
  margin: 0;
}
.main .section-map .overlay.active {
  display: flex;
}